.author-box::before,
.clearfix::before,
.entry::before,
.entry-content::before,
.footer-widgets::before,
.nav-primary::before,
.nav-secondary::before,
.pagination::before,
.site-container::before,
.site-footer::before,
.site-header::before,
.site-inner::before,
.widget::before,
.wrap::before {
	content: " ";
	display: table;
}

.author-box::after,
.clearfix::after,
.entry::after,
.entry-content::after,
.footer-widgets::after,
.nav-primary::after,
.nav-secondary::after,
.pagination::after,
.site-container::after,
.site-footer::after,
.site-header::after,
.site-inner::after,
.widget::after,
.wrap::after {
	clear: both;
	content: " ";
	display: table;
}

html {
	@apply antialiased;
}

body {
	@apply 
		text-main
		font-main;
	background-color: white;
	margin: 0;
	overflow-x: hidden;
}

a {
	@apply 
	text-link
	hover:text-hover 
	no-underline 
	transition-all 
	cursor-pointer;
}

a:focus,
a:hover {
	@apply 
	underline 
	cursor-pointer;
}

b,
strong {
	@apply 
	font-strong;
}

p {
	@apply 
	mb-p;
}

ol,
ul {
	@apply 
	mb-list;
}

li {
	@apply 
	list-none;
}

hr {
	@apply 
	text-line
	border-0 border-collapse border-b border-current clear-both my-5 mx-auto;
}

blockquote,
cite,
em,
i {
	@apply italic;
}

mark {
	@apply bg-gray-50 text-gray-900;
}

blockquote {
	@apply m-8;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	@apply 
	text-heading
	font-heading
	mt-0 mr-0 mb-5 ml-0;
}

h1 {
	@apply
	text-h1;
}

h2 {
	@apply
	text-h2;
}

h3 {
	@apply
	text-h3;
}

h4 {
	@apply
	text-h4;
}

h5 {
	@apply
	text-h5;
}

h6 {
	@apply
	text-h6;
}

embed,
iframe,
img,
object,
video,
.wp-caption {
	@apply max-w-full;
}

img {
	@apply h-auto align-top;
}

figure {
	@apply m-0;
}

table {
	@apply border-collapse mb-1 w-full;
	border-spacing: 0;
	word-break: break-all;
}

tbody {
	@apply border-b border-gray-200;
}

td,
th {
	@apply leading-loose text-left align-top;
}

td {
	@apply p-2;
}

tr {
	@apply border-t border-gray-200;
}

th {
	@apply font-semibold p-2;
}

input,
select,
textarea {
	@apply 
	bg-white 
	border border-solid border-gray-300 
	text-gray-900 text-lg 
	font-normal 
	p-4 
	w-full;
}

input:focus,
textarea:focus {
	@apply border border-solid border-gray-600 outline-none;
}

input[type="checkbox"],
input[type="image"],
input[type="radio"] {
	@apply w-auto;
}

:-ms-input-placeholder {
	@apply text-gray-900 opacity-100;
}

::placeholder {
	@apply text-gray-900 opacity-100;
}

button,
input[type="button"],
input[type="reset"],
input[type="submit"],
.site-container div.wpforms-container-full .wpforms-form input[type="submit"],
.site-container div.wpforms-container-full .wpforms-form button[type="submit"],
.button {
	@apply 
	bg-gray-900 
	border-0 
	rounded-md 
	text-white 
	cursor-pointer 
	text-base 
	font-semibold 
	px-4 py-8 
	text-center 
	no-underline 
	whitespace-normal 
	w-auto;
}

button:focus,
button:hover,
input[type="button"]:focus,
input[type="button"]:hover,
input[type="reset"]:focus,
input[type="reset"]:hover,
input[type="submit"]:focus,
input[type="submit"]:hover,
.site-container div.wpforms-container-full .wpforms-form input[type="submit"]:focus,
.site-container div.wpforms-container-full .wpforms-form input[type="submit"]:hover,
.site-container div.wpforms-container-full .wpforms-form button[type="submit"]:focus,
.site-container div.wpforms-container-full .wpforms-form button[type="submit"]:hover,
.button:focus,
.button:hover {
	@apply border-0 text-white;
}

.entry-content .button:focus,
.entry-content .button:hover {
	@apply text-white;
}

.button {
	@apply inline-block;
}

.site-container button:disabled,
.site-container button:disabled:hover,
.site-container input:disabled,
.site-container input:disabled:hover,
.site-container input[type="button"]:disabled,
.site-container input[type="button"]:disabled:hover,
.site-container input[type="reset"]:disabled,
.site-container input[type="reset"]:disabled:hover,
.site-container input[type="submit"]:disabled,
.site-container input[type="submit"]:disabled:hover {
	@apply bg-gray-200 border-0 text-gray-400 cursor-not-allowed;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button {
	@apply hidden;
}

.screen-reader-shortcut,
.screen-reader-text,
.screen-reader-text span {
	@apply border-0 h-px overflow-hidden w-px;
	clip: rect(0, 0, 0, 0);
	position: absolute !important;
	word-wrap: normal !important;
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus,
.widget_search input[type="submit"]:focus {
	@apply 
	bg-white 
	shadow-sm 
	text-gray-900 
	block 
	text-base 
	font-semibold 
	h-auto 
	py-4 px-1.5 
	no-underline 
	w-auto;
	clip: auto !important;
	z-index: 100000; /* Above WP toolbar. */
}

.more-link {
	@apply inline-block relative mb-8;
}